:root {
  --blue: #1fa2f1;
  --blueLight: #9bd1f9;
  --blueBackground: rgba(212, 237, 255, 0.6);
  --buttonHoverBg: #d4edff;
  --lightGrey: rgb(230, 236, 240);
  --spacing: 15px;
  --greyText: rgb(101, 119, 134);
  --greyButtonText: rgba(0, 0, 0, 0.34);
  --red: rgb(226, 34, 94);
  --redBackground: rgba(226, 34, 94, 0.1);
  --green: rgb(23, 191, 99);
  --greenBackground: rgba(23, 191, 99, 0.1);
}

html,
body {
  height: 100%;
  min-height: 100%;
  background-color: #fff;
  font-weight: 300;
}

ul {
  margin: 0;
}

* {
  outline: none !important;
}

a {
  color: inherit;
}

a:hover {
  color: inherit;
  text-decoration: none;
}

h1 {
  font-size: 19px;
  font-weight: 800;
  margin: 0;
}

nav {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  height: 100%;
}
nav.down-nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  flex-wrap: nowrap;
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  z-index: 8;
  overflow-x: scroll;
  background-color: #fbfbfb;
  height: fit-content;
  padding: 4% 0;
  box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px,
    rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
}

nav.down-nav a {
  width: 30px;
  height: 30px;
}

nav a {
  padding: 10px;
  font-size: 30px;
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #212529;
  position: relative;
}

nav a.blue {
  color: var(--blue);
}

nav a:hover {
  background-color: var(--buttonHoverBg);
  color: var(--blue);
  border-radius: 50%;
}

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100vh;
  position: relative;
}

.wrapper > .row {
  margin: 0;
  height: 100%;
}

button {
  background-color: transparent;
  border: none;
  color: var(--greyButtonText);
}

button:hover,
button i:hover,
button span:hover {
  cursor: pointer;
}

button i,
button span {
  pointer-events: none;
}

.mainSectionContainer {
  padding: 0;
  border-left: 1px solid var(--lightGrey);
  border-right: 1px solid var(--lightGrey);
  display: flex;
  flex-direction: column;
  
}


.titleContainer {
  height: 53px;
  padding: 5% var(--spacing);
  display: flex;
  align-items: center;
  justify-content: space-around;
  border-bottom: 1px solid var(--lightGrey);
  flex-shrink: 0;
}

.titleContainer .searchBarContainer {
  flex-basis: 70%;
}

.titleContainer .searchBarContainer #searchBox {
  margin-bottom: 0;
  /* border-radius: 2px;
  background-color: #fbfbfb;
  border: 2px solid var(--lightGrey); */
}

.titleContainer .searchBarContainer #searchBox:focus {
  border: 2px solid var(--blue);
}

.titleContainer .logo {
  flex-basis: 2%;
  color: var(--blue);
  font-size: 1.5rem;
}

.titleContainer .header-options {
  flex-basis: 10%;
  position: relative;
}

.titleContainer .header-options a, .titleContainer .header-options button {
  font-size: 1.4rem;
  color: #000;
}

.titleContainer .header-options a:first-of-type {
  margin-right: 15px;
}

.menu-popup{
  background-color: #fbfbfb;
  position: absolute;
  width: 50vh;
  right: 0;
  z-index: 10;
  height: fit-content;
  max-height: 80vh;
  /* overflow-y: scroll; */
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}

.postFormContainer {
  display: flex;
  padding: var(--spacing);
  border-bottom: 10px solid rgb(230, 236, 240);
  flex-shrink: 0;
}

.modal .postFormContainer {
  border: none;
  padding: 0;
  padding-top: var(--spacing);
}

.modal .post {
  padding: 0 0 var(--spacing) 0;
}

.userImageContainer {
  width: 50px;
  height: 50px;
}

.userImageContainer img {
  width: 100%;
  border-radius: 50%;
  background-color: white;
}

.textareaContainer {
  flex: 1;
  padding-left: var(--spacing);
}

.textareaContainer textarea {
  width: 100%;
  border: none;
  resize: none;
  font-size: 19px;
}

#submitPostButton {
  background-color: var(--blue);
  color: white;
  border: none;
  border-radius: 40px;
  padding: 7px 15px;
}

#submitPostButton:disabled {
  background-color: var(--blueLight);
}

.post {
  display: flex;
  flex-direction: column;
  padding: var(--spacing);
  cursor: pointer;
  border-bottom: 1px solid var(--lightGrey);
  flex-shrink: 0;
}

.mainContentContainer {
  flex: 1;
  display: flex;
  overflow-y: hidden;
}

.postContentContainer {
  padding-left: var(--spacing);
  display: flex;
  flex-direction: column;
  flex: 1;
}

.username,
.date {
  color: var(--greyText);
}

.displayName {
  font-weight: bold;
}

.postFooter {
  display: flex;
  align-items: center;
}

.postFooter .postButtonContainer {
  flex: 1;
  display: flex;
}

.postFooter .postButtonContainer button {
  padding: 2px 5px;
}

.header a:hover {
  text-decoration: underline;
}

.header a,
.header span {
  padding-right: 5px;
}

.postButtonContainer button:hover {
  background-color: #d4edff;
  color: var(--blue);
  border-radius: 50%;
}

.postButtonContainer.red button.active {
  color: var(--red);
}

.postButtonContainer.red button:hover {
  color: var(--red);
  background-color: var(--redBackground);
}

.postButtonContainer.green button.active {
  color: var(--green);
}

.postButtonContainer.green button:hover {
  color: var(--green);
  background-color: var(--greenBackground);
}

.postActionContainer {
  padding-left: 35px;
  font-size: 13px;
  color: var(--greyText);
  margin-bottom: 5px;
}

.replyFlag {
  margin-bottom: 5px;
}

.replyFlag a {
  color: var(--blue);
}

.post.largeFont .postBody,
.post.largeFont .postFooter {
  font-size: 23px;
}

.postContentContainer .header {
  display: flex;
}

.postContentContainer .header .date {
  flex: 1;
}

.errorMessage {
  padding: 0 var(--spacing);
  margin: 0;
  font: 1rem sans-serif;
  color: var(--red);
}

.betslip__errorMessage {
  padding: 7px var(--spacing);
  margin: 0;
  font: 1rem sans-serif;
  color: var(--red);
  background-color: rgb(229, 187, 187);
  text-align: center;
}

.coverPhotoSection {
  height: 180px;
  background-color: var(--blue);
  position: relative;
}

.profileHeaderContainer .userImageContainer {
  width: 132px;
  height: 132px;
  margin-left: var(--spacing);
  position: absolute;
  bottom: -66px;

  display: flex;
  align-items: center;
  justify-content: center;
}

.profileHeaderContainer .userImageContainer img {
  border: 4px solid #fff;
}

.profileHeaderContainer .profileButtonsContainer {
  text-align: right;
  padding: var(--spacing);
  min-height: 66px;
}

.profileButton,
.followButton {
  border: 1px solid var(--blue);
  color: var(--blue);
  font-weight: bold;
  padding: 5px 15px;
  border-radius: 60px;
  display: inline-block;
  margin-left: var(--spacing);
}

.profileButton:hover,
.followButton:hover {
  background-color: var(--blueBackground);
}

.followButton.following {
  background-color: var(--blue);
  color: #fff;
}

.profileHeaderContainer .userDetailsContainer {
  display: flex;
  flex-direction: column;
  padding: 0 var(--spacing);
}

.followersContainer .value {
  font-weight: bold;
  margin-right: 5px;
}

.followersContainer span:not(.value) {
  color: var(--greyText);
}

.followersContainer a {
  margin-right: 15px;
}

.followersContainer a:hover {
  border-bottom: 1px solid #000;
}

.tabsContainer {
  display: flex;
  border-bottom: 1px solid var(--lightGrey);
  flex-shrink: 0;
}

.tab {
  flex: 1;
  height: 52px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--greyText);
  font-weight: bold;
}

.tab.active {
  color: var(--blue);
  border-bottom: 2px solid var(--blue);
}

.tab:hover {
  color: var(--blue);
  background-color: var(--blueBackground);
}

.noResults {
  padding: var(--spacing);
}

.resultsContainer {
  display: flex;
  flex-direction: column;
  position: relative;
}

.resultsContainer .user {
  padding: var(--spacing);
  display: flex;
  border-bottom: 1px solid var(--lightGrey);
}

.user .userDetailsContainer {
  flex: 1;
  padding: 0 var(--spacing);
}

.profilePictureButton,
.coverPhotoButton {
  position: absolute;
  font-size: 50px;
  color: rgba(0, 0, 0, 0.6);
  display: none;
}

.userImageContainer:hover .profilePictureButton,
.coverPhotoSection:hover .coverPhotoButton {
  display: block;
}

.coverPhotoContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.coverPhotoContainer img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.imagePreviewContainer {
  width: 120px;
  max-height: 80px;
  margin-top: 20px;
  margin-bottom: 10px;
  overflow: none;
}

#imagePreview,
#coverPreview {
  /* width: 100%; */
  object-fit: cover;
  display: block;

  /* This rule is very important, please don't ignore this */
  max-width: 100%;
}

.pinButton.active {
  color: var(--blue);
}

.pinButton:hover {
  color: var(--blue);
}

.deleteButton:hover {
  color: var(--red);
}

.pinnedPostText {
  font-size: 12px;
  color: var(--greyText);
}

.pinnedPostContainer {
  border-bottom: 10px solid rgb(230, 236, 240);
}

.searchBarContainer {
  position: relative;
  color: var(--greyText);
  padding: 10px var(--spacing);
}

.searchBarContainer i {
  position: absolute;
  top: 20px;
  left: 28px;
}

.searchBarContainer #searchBox {
  height: 36px;
  width: 100%;
  padding: 5px 15px 5px 40px;
  border-radius: 50px;
  background-color: var(--lightGrey);
  border: none;
  color: var(--greyText);
}

.chatPageContainer {
  display: flex;
  flex-direction: column;
  min-height: 0;
  flex: 1;
  flex-basis: 0;
}

.chatTitleBar {
  border-bottom: 1px solid var(--lightGrey);
  display: flex;
  align-items: center;
  min-height: 60px;
  padding: 10px;
}

.chatTitleBar label {
  margin: 0 10px 0 0;
}

#userSearchTextbox {
  border: none;
  flex: 1;
  font-weight: 200;
  min-width: 350px;
}

#createChatButton {
  border: none;
  background-color: var(--blue);
  color: #fff;
  padding: 7px 20px;
  margin: 10px auto;
  border-radius: 40px;
}

#createChatButton:disabled {
  background-color: var(--lightGrey);
  color: var(--greyText);
}

.selectedUser {
  font-weight: 300;
  background-color: #cbe5fe;
  color: #0084ff;
  padding: 5px;
  border-radius: 3px;
  margin: 5px 5px 0 0;
  display: inline-block;
}

.resultListItem {
  padding: 7px;
  display: flex;
  align-items: center;
  flex-shrink: 0;
  border-bottom: 1px solid var(--lightGrey);
}

.resultListItem:hover {
  background-color: #f2f2f2;
}

.resultsDetailsContainer {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.resultsDetailsContainer .heading {
  font-weight: 500;
}

.resultsDetailsContainer .subText {
  color: var(--greyText);
  font-size: 14px;
}

.resultListItem img {
  height: 100%;
  width: 100%;
  border-radius: 50%;
}

.resultsImageContainer {
  height: 40px;
  width: 40px;
  position: relative;
  margin-right: 10px;
  display: flex;
  align-items: center;
  padding: 5px;
}

.groupChatImage img {
  height: 65%;
  width: 65%;
  position: absolute;
  bottom: 0;
  margin: 0;
  border: 2px solid #fff;
}

.groupChatImage img:first-of-type {
  top: 0;
  right: 0;
}

.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.chatTitleBarContainer {
  border-bottom: 1px solid var(--lightGrey);
  display: flex;
  align-items: center;
  padding: var(--spacing);
}

#chatName {
  width: 100%;
  border: 1px solid transparent;
  padding: 0 5px;
}

#chatName:hover {
  border: 1px solid var(--lightGrey);
  cursor: text;
}

.chatContainer {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow-y: hidden;
}

.chatContainer .footer {
  display: flex;
  padding: var(--spacing);
  flex-shrink: 0px;
}

.chatContainer .footer textarea {
  flex: 1;
  resize: none;
  background-color: rgba(0, 0, 0, 0.05);
  border-radius: 18px;
  border: none;
  padding: 8px 12px;
  height: 38px;
}

.chatContainer .footer button {
  background-color: transparent;
  color: var(--blue);
  font-size: 24px;
}

.chatMessages {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  padding: var(--spacing);
}

.chatImagesContainer {
  display: flex;
  flex-direction: row-reverse;
  height: 100%;
  margin-right: 10px;
}

.chatImagesContainer img {
  width: 40px;
  height: 40px;
  border: 2px solid #fff;
  border-radius: 50%;
}

.chatImagesContainer .userCount {
  height: 40px;
  width: 40px;
  background-color: #f1f1f1;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  border: 2px solid #fff;
}

.chatImagesContainer img:not(:last-child),
.chatImagesContainer .userCount {
  margin-left: -10px;
}

#chatNameTextbox {
  width: 100%;
}

.chatMessages .message {
  padding-bottom: 2px;
  list-style: none;
  display: flex;
  align-items: flex-end;
  flex-shrink: 0;
}

.typingDots img,
.chatMessages .message .messageBody {
  background-color: #f1f0f0;
  padding: 6px 12px;
  border-radius: 18px;
  font-size: 14px;
}

.typingDots img {
  height: 35px !important;
}

.typingDots {
  padding: var(--spacing);
  padding-bottom: 0;
  display: none;
}

.chatMessages .message .messageContainer {
  display: flex;
  flex-direction: column;
  max-width: 55%;
}

.chatMessages .message.mine .messageContainer {
  align-items: flex-end;
}

.chatMessages .message.theirs .messageContainer {
  align-items: flex-start;
}

.chatMessages .message.mine {
  flex-direction: row-reverse;
}

.chatMessages .message.mine .messageBody {
  background-color: var(--blue);
  color: #fff;
}

.chatMessages .message.mine.first .messageBody {
  border-bottom-right-radius: 2px;
}

.chatMessages .message.mine:not(.first):not(.last) .messageBody {
  border-bottom-right-radius: 2px;
  border-top-right-radius: 2px;
}

.chatMessages .message.mine.last .messageBody {
  border-bottom-right-radius: 18px;
}

.chatMessages .message.mine.last:not(.first) .messageBody {
  border-top-right-radius: 2px;
}

.chatMessages .message.theirs.first .messageBody {
  border-bottom-left-radius: 2px;
}

.chatMessages .message.theirs:not(.first):not(.last) .messageBody {
  border-bottom-left-radius: 2px;
  border-top-left-radius: 2px;
}

.chatMessages .message.theirs.last .messageBody {
  border-bottom-left-radius: 18px;
}

.chatMessages .message.theirs.last:not(.first) .messageBody {
  border-top-left-radius: 2px;
}

.senderName {
  color: rgba(0, 0, 0, 0.4);
}

.chatMessages .imageContainer {
  height: 24px;
  width: 24px;
  margin-right: 7px;
}

.chatMessages img {
  height: 100%;
  border-radius: 50%;
  vertical-align: bottom;
}

.chatMessages .message.last {
  margin-bottom: 7px;
}

.loadingSpinnerContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.loadingSpinnerContainer img {
  width: 50%;
  max-width: 75px;
}

.resultListItem.active {
  background-color: var(--blueBackground);
}

.resultListItem.notification * {
  pointer-events: none;
}

#notificationBadge,
#messagesBadge {
  background-color: var(--red);
  height: 25px;
  width: 25px;
  border-radius: 50%;
  position: absolute;
  top: 0;
  right: 0;
  color: white;
  font-size: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
}

#notificationBadge.active,
#messagesBadge.active {
  visibility: visible;
}

#notificationList {
  position: fixed;
  top: 5px;
  right: 5px;
  width: 350px;
  background-color: white;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
}

#notificationList .active {
  background-color: white;
}

h1.advertisement {
  text-align: center;
  padding: 20px 0;
}

/* Custom Flex */

.flex {
  display: flex;
  justify-content: space-between;
}

.game-page__title {
  padding: 10px 20px;
  letter-spacing: 2px;
  text-transform: uppercase;
  border-bottom: 1px solid var(--blue);
  box-shadow: 0 1.5px 1.5px rgb(0 0 0 / 0.2);
}


.sport__advertisement{
  width: 100%;
  max-height: 100px;
}

.sports-carousel-container .carousel-inner{
  overflow: hidden;
}

.sports-carousel-container .carousel-inner .carousel-item{
  max-height: 30vh;
  min-height: 30vh;
  background-size: cover !important;
  background-position: center !important;
  background-repeat: no-repeat !important;
}

/* .sports-carousel-container .carousel-inner .carousel-item img{
  min-width: 100%;
  height: fit-content;
  /* max-height: 100%;  */
  /* object-fit: contain !important;
  object-position: center;
} */ 

/* Sports Card */

.sport-container {
  display: flex;
  flex-direction: column;
  cursor: pointer;
  border-bottom: 3px solid var(--lightGrey);
  flex-shrink: 0;
  transition: 0.2s ease-in-out;
}

.sport-container:hover {
  background-color: var(--blueLight);
  box-shadow: 0 1px 1px rgb(0 0 0 / 0.2);
}

.sport-container:hover .sport__header-container {
  border-bottom: 1px solid white;
}

.sport-container:hover .sport__header__icon,
.sport-container:hover .sport__score {
  border-right: 1px solid white;
}

.sport-container:hover .sport__headers,
.sport-container:hover .sport__odds {
  border-left: 1px solid white;
}
.sport__odds__single.active{
  background-color: var(--blue);
}




.sport__header-container {
  display: flex;
  align-items: center;
  padding: 7px 0;
  border-bottom: 1px solid var(--lightGrey);
  transition: 0.2s ease-out;
}

.sport__header__icon {
  display: flex;
  flex-basis: 10%;
  border-right: 1px solid var(--lightGrey);
  transition: 0.2s ease-out;
  font-size: 19px;
  letter-spacing: 1px;
  font-weight: 600;
}

.sport__league {
  display: flex;
  flex-basis: 70%;
  justify-content: flex-start;
  padding-left: 10px;
  font-size: 18px;
  letter-spacing: 1px;
  font-weight: 600;
}

.sport__headers {
  display: flex;
  flex-basis: 30%;
  justify-content: space-evenly;
  border-left: 1px solid var(--lightGrey);
  transition: 0.2s ease-out;
  font-size: 18px;
  letter-spacing: 1px;
  font-weight: 600;
}

.sportContentContainer {
  display: flex;
  align-items: center;
  padding: 10px 0;
}

.sport__score {
  flex-basis: 5%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-right: 1px solid var(--lightGrey);
  transition: 0.2s ease-out;
  font-size: 16px;
  letter-spacing: 1px;
  font-weight: 400;
  padding-left: 7px;
}

.sport__teams {
  flex-basis: 65%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  font-size: 16px;
  letter-spacing: 1px;
  font-weight: 400;
}

.sport__odds {
  display: flex;
  flex-basis: 30%;
  justify-content: space-evenly;
  border-left: 1px solid var(--lightGrey);
  transition: 0.2s ease-out;
  font-size: 16px;
  letter-spacing: 1px;
  font-weight: 400;
}


.sportContentContainer.quiz-view{
  display: flex;
  justify-content: space-between;
  padding: 1% 4%;
}

.sportContentContainer__teams-container{
  display: flex;
  flex-direction: column;
}

.sportContentContainer__teams-items{
  display: flex;
  align-items: center;
  margin: 5px;
}

.sportContentContainer__teams-items i{
  margin-right: 10px;
}

.sportContentContainer__teams-items p{
  margin: 0;
  padding: 0;
  font-weight: 400;
}

.sportContentContainer__btn-container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.sportContentContainer__btn-time{
  margin-bottom: 5px;
  padding: 0;
}

.sportContentContainer__btn-item{
  padding: 5px 30px;
  background-color: var(--blue);
  color: #fbfbfb;
  border-radius: 5px;
  letter-spacing: 1px;
  
}

.suspended {
  display: flex;
  flex-basis: 30%;
  justify-content: center;
  transition: 0.2s ease-out;
  font-size: 16px;
  letter-spacing: 1px;
  font-weight: 400;
}

.betSelectionbtn {
  background-color: var(--blue);
  padding: 3px 15px;
  color: #ffffff;
  border-radius: 5px;
  box-shadow: 0 1px 1px rgb(0 0 0 / 0.2);
}

.betSelectionbtn.active{
  background-color: var(--lightGrey);
  color: var(--blue);
}

.gamepage__tab__container {
  display: flex;
  justify-content: flex-start;
  box-shadow: 0 1px 1px rgb(0 0 0 / 0.2);
  margin-bottom: 2px;
  
}

.gamepage__tab__container.quiz-tab{
  flex-wrap: wrap;
}

.gamepage__tab__container.quiz-tab .gamepage__tab__item{
  flex-basis: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  border-bottom: 1px solid var(--lightGrey);
  border-right: 1px solid var(--lightGrey);
}

.gamepage__tab__container__opener{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 10px;
  cursor: pointer;
  font-size: .9rem;
  font-weight: 500;
}

.gamepage__tab__container__opener p{
  border-bottom: 1px solid #000;
}

.gamepage__tab__container.quiz-tab .gamepage__tab__item.active{
  font-size: .9rem;
}

@media screen and (max-width: 900px) {
  .gamepage__tab__container {
    overflow-x: scroll;
  }
}

.gamepage__tab__item {
  flex: content;
  padding: 5px 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 400;
  letter-spacing: 1px;
  cursor: pointer;
}

.gamepage__tab__item.active {
  box-shadow: 1px 1px 1px rgb(0 0 0 / 0.2);
  background-color: var(--blueBackground);
}


.league-name__container{
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  padding: 2%;
}
.league-name__item{
  padding: 10px;
  border: 1px solid var(--blue);
  border-radius: 5px;
  font-weight: 400;
  cursor: pointer;
}

.league-name__item:hover{
  background-color: var(--blue);
  color: #fbfbfb;
}

.league-name__item.active{
  background-color: var(--blue);
  color: #fbfbfb;
}