.mybet__container{
    display: flex;
    flex-direction: column;
    padding: 2% 4%;
}

.mybet__heading{
    display: flex;
    padding: 3% 2%;
    justify-content: center;
    align-items: center;
    font-size: 1.7rem;
    font-weight: 600;
    letter-spacing: 1px;
    font-family: 'Arial';
}



/* HEADER */

.mybet__header{
    display: flex;
    flex-wrap: nowrap;
    padding: 1%;
    background-color: var(--blueBackground);
    
}


.mybet__header__details{
    flex-basis: 40%;
}

.mybet__header__date{
    flex-basis: 15%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.mybet__header__stake{
    flex-basis: 15%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.mybet__header__return{
    flex-basis: 15%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.mybet__header__odds{
    flex-basis: 15%;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* BODY */

.mybet__body{
    display: flex;
    flex-wrap: nowrap;
    padding: 1%;
    font-size: .9rem;
    border-bottom: 1px solid var(--lightGrey);
}


.mybet__body.multi{
    background: #ffe69cc9;
    border-left: 3px solid #ffc61a;
}

.mybet__body.system{
    background: rgba(255, 132, 132, 0.618);
    border-left: 3px solid rgb(197, 6, 6);
}

.mybet__body__details{
    flex-basis: 40%;
}

.mybet__body__details__option{
    margin: 0;
    padding: 0;
    margin-bottom: 5px;
}

.mybet__body__details__stake{
    margin: 0;
    padding: 0;
}

.mybet__body__date{
    flex-basis: 15%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.mybet__body__stake{
    flex-basis: 15%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.mybet__body__return{
    flex-basis: 15%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.mybet__body__odds{
    flex-basis: 15%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.mybet__body__odds__btn{
    margin-top: 5px;
    background-color: var(--blue);
    padding: 1px 5px;
    color: #ffffff;
    border-radius: 5px;
    font-size: 90%;
    box-shadow: 0 1px 1px rgb(0 0 0 / 0.2);
}

/* FOOTER */

.mybet__footer{
    display: flex;
    flex-wrap: nowrap;
    padding: 1%;
}

.mybet__footer__details{
    flex-basis: 70%;
}

.mybet__footer__stake{
    flex-basis: 15%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.mybet__footer__return{
    flex-basis: 15%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.mybet__footer__odds{
    flex-basis: 15%;
    display: flex;
    justify-content: center;
    align-items: center;
}


.multibetcontainer{
    cursor: pointer;
    
}

.multibetcontaier__collapsed{
    display: none;
}

.multibetcontaier__collapsed.active{
    display: block;
    animation-direction: normal;
}