.gamescore__container {
  display: flex;
  flex-direction: column;
  padding: 10%;
  justify-content: center;
  align-items: center;
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(9, 9, 121, 1) 22%,
    rgba(0, 212, 255, 1) 100%
  );
  color: #ffffff;
}

.gamescore__container__date{
  display: flex;
  font-size: 1rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.gamescore__flex-container {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.gamescore__flex-item {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.gamescore__flex-item.teamname {
  font-size: 1.7rem;
  font-weight: 600;
  letter-spacing: 2px;
  text-align: center;
}

.gamescore__flex-item.vs {
  font-size: 2.5rem;
  font-weight: 400;
  letter-spacing: 2px;
}

.gamescore__flex-item.score {
  margin-top: 10px;
  font-size: 1rem;
  font-weight: 400;
  letter-spacing: 2px;
}

.fixed-modal-area {
  width: 100%;
  height: 250px;
  max-height: 250px;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
}

.fixed-modal-item {
  display: flex;
  padding: 1% 4%;
  letter-spacing: 1.5px;
  font-weight: 400;
  display: flex;
  justify-content: center;
  align-items: center;
}

.fixed-modal-item:nth-child(odd) {
  background-color: var(--blue);
  color: #ffffff;
}

.gamescore__score__commentary {
  cursor: pointer;
}
