.betslip__container {
    position: fixed;
    bottom: 7%;

     left: 50%;  
     transform: translate(-50%, -7%);
    z-index: 9;
    width: 100%;
    max-width: 460px;
    display: flex;
    overflow: none;
    flex-direction: column;
    background-color: #fbfbfb;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.betslip__success__container{
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 10;
    overflow: hidden;
    background-color: var(--blue);
}

.betslip__success__text{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    display: flex;
    flex-direction: column;
}

.betslip__success__title{
    font-size: 2.3rem;
    font-weight: 700;
    color: #fbfbfb;
    text-transform: capitalize;
    text-align: center;
}

.betslip__success__btn{
    text-align: center;
    padding: 8px 10px;
    background-color: #fbfbfb;
    color:#000000;
    border-radius: 10px;
    margin-top: 30px;
}

.betslip__badge{
    padding: 3px 0;
    font-size: 1.2rem;
    font-weight: 400;
    border-bottom: 1px solid var(--lightGrey);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
}

.betslip__badge .form-check{
    display: flex;
    align-items: center;
    font-size: 1rem;
    cursor: pointer !important;
}

.betslip__badge .form-check *{
    cursor: pointer !important;   
}
.betslip__stakeType{
    display: flex;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.betslip__stakeType__item{
    flex: 1;
    padding: 2% 5%;
    font-size: 1rem;
    font-weight: 400;
    cursor: pointer;
}

.betslip__stakeType__item.active{
    background-color: var(--greyText);
    color:#fbfbfb;
}

.betslip__header {
    
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2% 5%;
    background-color: var(--blue);
    color: #fbfbfb;
}

.betslip__header__text {
    flex-basis: 60%;
    display: flex;
    justify-content: flex-start;
}

.betslip__header__stake{
    flex-basis: 10%;
    display: flex;

}

.betslip__header__stake .betslip__stake{

    display: flex;
    padding: 1% 3%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.betslip__header__stake .betslip__stake .betslip__stake__toreturn{
    font-size: .8rem;
    font-weight: 400;
}

.betslip__header__icon{
    flex-basis: 1%;
    min-height: 20px;
    min-width: 20px;
    cursor: pointer;
    /* display: flex;
    align-self: flex-end; */
}
.betslip__title{
    margin-right: 10%;
    margin-left: 5%;
}

.betslip__body__container{
    max-height: 50vh;
    overflow-y: scroll;
    overflow-x: hidden !important;
}

.betslip__body {
    padding: 2% 5%;
    box-shadow: rgb(204, 219, 232) 3px 3px 6px 0px inset, rgba(255, 255, 255, 0.5) -3px -3px 6px 1px inset;
}

.betslip__body p {
    font-size: 1.1rem;
    font-weight: 400;
    margin: 0;
    margin-bottom:5px;
    padding: 0;
}

.betslip__odd span{
    /* font-size: 1.2rem; */
    color: var(--red);
}

.betslip__footer {
    display: flex;
    overflow: none;
    position: relative;
}

#stripes {
    height: 100%;
    width: 100%;
    background-color:var(--blue);
    position: absolute;
    top: 0;
    left: 0;
  }

  div#stripes:before {
    content: 'Wait a moment...';
    font-size: 20px;
    color: #fff;
    text-align: center;
    height: 100%;
    width: 100%;
    font-weight: bold;
    display: table;
    padding-top: 5px;
    letter-spacing: 2px;
}
  
  .stripes {
      background: repeating-linear-gradient(
        45deg,
        transparent,
        transparent 10px,
        #000 10px,
        #000 20px
    );

    background-size: 200% 200%;
    animation: barberpole 10s linear infinite;
  }

  @keyframes barberpole {
    100% {
      background-position: 100% 100%;
    }
  }

.betslip__stake{
    flex: 1;
    padding: 2% 2%;
    font-weight: 600;
    text-align: center;
    display: flex;
    align-items: center;
}

.betslip__btn__saving{
    flex: 1;
    /* padding: 2% 2%; */
    font-weight: 600;
    text-align: center;
}

.betslip__btn__saving:disabled, .betslip__btn__saving:disabled:hover {
    background-color: var(--lightGrey);
    color: var(--greyText);
    pointer-events: none;
  }


.betslip__btn__saving .totals{
    display: flex;
    flex-direction: column;
    font-size: .8rem;
    font-weight: 400;
}

.betslip__btn__saving .betslip__btn__saving__text{
    flex-basis: 35%;
}
.betslip__btn__saving .totals{
    flex-basis: 60%;
}

.betslip__header__stake .betslip__stake{
    font-weight: 600;
    text-align: center;   
}

.betslip__header__stake .betslip__stake{
    padding: 0 5% !important;
    
}

.betslip__stake {
    background-color: var(--red);
    color: #fbfbfb;
}

/* .betslip__stake:hover .betslip__stake__input, .betslip__stake__input:focus{
    background-color: var(--red);
} */
.betslip__stake__input{
    border: none;
    background-color:var(--red);;
    color:#fbfbfb;
}

.betslip__header__stake .betslip__stake__input{
    background-color:#3881ad;
    max-width: 100%;
}

.betslip__stake__input::placeholder{
    color: #fbfbfb;
}

/* Chrome, Safari, Edge, Opera */
.betslip__stake__input::-webkit-outer-spin-button,
.betslip__stake__input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
.betslip__stake__input {
  -moz-appearance: textfield;
}

/* .betslip__stake:hover {
    background-color: rgb(151, 18, 60);
} */

.betslip__btn__saving, .betslip__header__stake .betslip__stake  {
    background-color: var(--blue);
    color: #fbfbfb;
}

.betslip__btn__saving{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.betslip__btn__saving:hover {
    background-color: #0f78b8;
}

